@use 'base/mixins' as m;
@use 'base/variables' as v;

.recuperar {
    .titulo2 {
        @include m.header(v.$azul);
    }
    .formulario__boton {
        @include m.formbot(v.$azul);
        border-radius: .5rem;
        min-width: 100%;
        margin: 0;
        @include m.tablet { 
            min-width: 18rem;
        }
    }
    .opciones-recupera {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        @include m.tablet { 
            margin-top: 2rem;
            flex-direction: row;
            justify-content: space-between;
        }
    }
}