@use 'base/mixins' as m;
@use 'base/variables' as v;

.mensaje {
    .titulo2 {
        @include m.header(v.$azulf);
    }
    .instrucciones__descripcion {
        color: v.$azul;
        font-weight: 900;
        font-size: 2rem;
        text-align: center;
        line-height: 1.2;
    }
    .formulario__boton {
        @include m.formbot(v.$azulf);
        border-radius: .5rem;
    }
}