@use 'base/mixins' as m;
@use 'base/variables' as v;

.auth-bg {
    .pagina {
        max-width: 120rem;
        margin: 0 auto;
        width: 90%;
            @include m.tablet { 
                max-width: 100%;
                margin: auto;
                width: 100%;
            }
        .descripcion {
            font-family: v.$fsecundaria;
            font-weight: 900;
            font-size: 2.5rem;
            text-align: center;
            line-height: 1;
        }
        &__grid {
            min-height: 100vh;
            @include m.tablet { 
                @include m.grid1-2(0);
            }
        }
        .logo-site {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            @include m.tablet { 
                padding: 0 0 0 2rem;
            }
            .titulo {
                padding: 0;
                background-image: url(../img/logo-task.png);
                background-size: contain;
                width: 30rem;
                height: 15rem;
                background-repeat: no-repeat;
                background-position: center center;
                margin: 2rem 0;
                @include m.tablet { 
                    width: 30rem;
                    height: 20rem;
                    margin: 4rem 0;
                }
            }
        }
        .instrucciones {
            display: flex;
            flex-direction: column;
            justify-content: center;
        }
    }
    
}
